import React, { ReactElement } from 'react';
import styles from '../css/components.module.css';

interface TabProps {
	name: string
	nameDict: {[key: string]: string | JSX.Element }
	setName: (name: string) => void
	static?: boolean
}

export default function TabSection(props: TabProps): ReactElement {

	return (				
		<div className={[styles.tabSection].join(' ')} style={{gridAutoColumns: props.static ? '1fr' : undefined}}>
			{Object.entries(props.nameDict).map(([k, v], i) => {
				if (!v) return null;
				return <div
					key={[k, i].join('.')}
					onClick={() => props.setName(k as string)}
					className={[styles.tab, k === props.name ? styles.active : ''].join(' ')}
				>
					{v}
				</div>
			})}
		</div>
	);
}