import React, { ReactElement, useCallback, useState, useEffect, useMemo, createRef, FormEvent, useContext, useReducer } from 'react';
import { GET } from '../../utils/requests';
import { Player, TournamentSettings, ID, History, PairingObject, TournamentStatus } from '../../interfaces';

import styles from '../../css/profile.module.css';
import CollapsableSection, { SectionBreak } from '../../components/CollapsableSection';
import { AiOutlineClose } from 'react-icons/ai';
import { DisplayNameContext } from '../../Contexts';
import Clashes from './Clashes';
import Bonuses from './Bonuses';
import Search from '../../components/Search';
import countries from '../../assets/countries.json';
import { EditableField } from '../../components';
import { capitalise } from '../../utils/prototype';
const nationalities = Object.entries(countries).map(([k, v]) => {
	return {
		name: v,
		id: k
	};
});

export interface ProfileProps {
	id: string
	status: TournamentStatus
	playerID: string
	setDisplayPlayer: (id: string) => void
	playerDict: Map<ID, Player>
	modifyPlayerDict: (p: Player, hash: string) => void
	settings: TournamentSettings
	playerDictKey?: string
}

export default function Profile(props: ProfileProps): ReactElement {
    
	const [playerID, setPlayerID] = useState('');
	const [player, setPlayer] = useState(null as Player);
	const players = useMemo(() => Array.from(props.playerDict.values()), [props.playerDict, props.playerDictKey]);
	const getDisplayName = useContext(DisplayNameContext);

	const updatePlayerFromProps = (refetch?: boolean) => {
		setPlayerID(playerID);		
		if (!props.playerID) {
			setPlayer(null as Player);
			return;
		}
		if (refetch === false) {
			setPlayer(props.playerDict.get(props.playerID));
		} else {
			return GET({
				url: '/tournament/' + props.id + '/fetchPlayer',
				params: {
					id: props.playerID
				}
			})
				.then((p: Player) => {
					if (!p.histories[0]) p.histories[0] = null;
					setPlayer(p);
					if (refetch) props.modifyPlayerDict(p, Math.random().toString().slice(2, 9));
				});
		}
	};
    
	useEffect(() => {
		updatePlayerFromProps();
	}, [props.playerID, props.playerDictKey]);
    
	const renderEntry = useCallback(([k, v]: [string, any], i: number, arr: any[], cap?: boolean): ReactElement => {
		if (k === 'Playing' && v) {
			return (
				<div className={styles.row} key={['row', i].join('.')}>
					<div className='fieldBox'>{k + ':'}</div>
					<div
						className='playerInfo'
						id={v as string}
						onClick={(e: React.MouseEvent<HTMLDivElement>) => props.setDisplayPlayer((e.target as HTMLDivElement).id)}
					>{getDisplayName(v)}</div>
				</div>
			);
		} else {
			return (
				<div className={styles.row} key={['row', i].join('.')}>
					<div className='fieldBox'>{(cap ? capitalise(k.trim()) : k)  + ':'}</div>
					{React.isValidElement(v) ? v : <div className='fieldBox'>{v !== null && v!== '' ? v : '\u200b'}</div>}
				</div>
			);
		}
	}, [props.setDisplayPlayer, getDisplayName]);

	if (!player) return <div className={styles.profileContainer}>
		<Search
			onSubmit={props.setDisplayPlayer}
			options={Array.from(props.playerDict.values())}
			getName={(id) => getDisplayName(id as any as string | Player)}
			showHistory={true}
			styles={styles}
		/>
	</div>;
	
	const profile = !player ? {} : {
		'Unique ID': player.id,
		Status: player.active !== false ? 'Active' : 'Inactive',
		Rating: player.rating,
		Nationality: <EditableField
			content={player.nationality}
			url={['tournament', props.status.id, 'updatePlayer'].join('/')}
			inputType='text'
			onSubmitSuccess={(e) => {
				updatePlayerFromProps(true);
				return e;
			}}
			additionalStyles={{
				editableField: 'fieldBox'
			}}
			name='nationality'
			additionalInputProps={{
				id: player.id
			}}
			show={{ submit: true }}
			search={{
				options: nationalities,
				getName: (p) => (p as {id: string, name: string}).name
			}}
		/>,
		'Performance rating': player.performanceRating,
		Playing: player.isPlaying
	};
	let history = player.histories;

	const total = (Math.max(player.histories.length, 1) - 1 - (player.isPlaying ? 1 : 0)).toString();
	const totalGames = player.histories.reduce((acc, curr) => {
		if (!curr) return acc;
		acc += curr.played;
		return acc;
	}, 0);
	const scores = !player ? {} : {
		Score: player.score.toString() + ' / ' + total,
		'Game score': player.gamePoints.toString() + ' / ' + totalGames.toString(),
		'Expected score': player.expectedScore.toFixed(2),
		'Expected game score': player.expectedGameScore.toFixed(2) + ' / ' + totalGames,
	};
	if (!props.settings.displayPoints) {
		delete scores['Game score'];
		delete scores['Expected game score'];
	}

	return (
		<>
			<div className={[styles.profileContainer, 'scrollable'].join(' ')}>
				<div>
					<div className={styles.infoSection}>
						<div className={styles.row}>
							<div
								style={{
									gridRow: '1 / span 1',
									gridColumn: '1 / span 2'
								}}
								className={['playerInfo', styles.name].join(' ')}
								id={player.id}
								onClick={(e: React.MouseEvent<HTMLDivElement>) => props.setDisplayPlayer((e.target as HTMLDivElement).id)}
							>{getDisplayName(player)}</div>
						</div>
						{Object.entries(profile).map(renderEntry)}
					</div>
				</div>
				{player.contact ? <CollapsableSection
					header='Contact'
					useInternalState={true}
					style={{ gridColumn: '1 / span 2' }}
				>
					{Object.entries(player.contact).map((e, i, arr) => renderEntry(e, i, arr, true))}
				</CollapsableSection> : null}
				{player.meta ? <CollapsableSection
					header='Information'
					useInternalState={true}
					style={{ gridColumn: '1 / span 2' }}
				>
					{Object.entries(player.meta).map((e, i, arr) => renderEntry(e, i, arr, true))}
				</CollapsableSection> : null}
				<CollapsableSection
					header='Score'
					useInternalState={true}
					style={{ gridColumn: '1 / span 2' }}
				>
					{Object.entries(scores).map(renderEntry)}
				</CollapsableSection>
				{props.settings.allowClashes ? <CollapsableSection
					header='Forbidden Pairings'
					useInternalState={true}
					style={{ gridColumn: '1 / span 2' }}
				>
					<Clashes {...props} players={players} player={player} />
				</CollapsableSection> : null}
				<CollapsableSection
					header='Results'
					useInternalState={true}
					style={{ gridColumn: '1 / span 2' }}
				>
					{history.map((minPlayer: History, i: number): ReactElement => {
						if (i === 0 || !minPlayer) return null;
						let { id, colour } = minPlayer;
						let result = minPlayer[props.settings.displayPoints];
						return (
							<div className={styles.resultsRow} key={['row', 'history', i].join('.')}>
								<div className='fieldBox'>{i}</div>
								<div
									className='playerInfo'
									id={id}
									onClick={(e: React.MouseEvent<HTMLDivElement>) => props.setDisplayPlayer((e.target as HTMLDivElement).id)}
								>{getDisplayName(id)}</div>
								<div className='fieldBox'>{colour || '\u200b'}</div>
								<div className='fieldBox'>{typeof result !== 'undefined' && result !== null ? result : '\u200b'}</div>
							</div>
						);
					})}
				</CollapsableSection>
				{props.settings.allowBonuses ? <CollapsableSection
					header='Bonus points'
					useInternalState={true}
					style={{ gridColumn: '1 / span 2' }}
				>
					<Bonuses {...props} players={players} player={player} update={updatePlayerFromProps}/>
				</CollapsableSection> : null}
			</div>
			<div className={['button', styles.closeButton].join(' ')} onClick={() => props.setDisplayPlayer('')}>
				<AiOutlineClose /> Close profile
			</div>
		</>
	);
}